.header {
  background: #aa0601;
  justify-content: space-between;
  padding: 0px 88px;
  flex-wrap: wrap; }
  .header .logo {
    padding: 30px 0px; }
    .header .logo a {
      background: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true") center;
      background-size: cover;
      display: block;
      width: 80px;
      height: 48px;
      text-indent: 101%;
      overflow: hidden;
      white-space: nowrap; }
  .header .nav-bar {
    align-items: center;
    flex-wrap: wrap; }
    .header .nav-bar li a {
      padding: 40px 0px;
      padding-left: 64px;
