@media (max-width: 992px) {
  .header {
    padding: 0px 28px; }
    .header .nav-bar li a {
      padding-left: 40px; } }

@media (max-width: 767px) {
  .header {
    flex-direction: column;
    padding: 0px;
    text-align: center; }
    .header .logo {
      margin: 0 auto;
      padding: 10px 0px;
      align-items: center; }
      .header .logo a {
        display: block;
        width: 48px;
        height: 29px;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap; }
    .header .nav-bar li {
      width: 50%;
      border: 1px solid #fff; }
      .header .nav-bar li a {
